import { Component, h, Host, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-button-pair',
  styleUrl: 'orderButtonPair.css'
})
export class OrderButtonPair {
    @Prop() left: string = "";
    @Prop() linkLeft: string = "";

    @Prop() right: string = "";
    @Prop() linkRight: string = "";


    render() {

        return <Host>
            <ks-button rounded link={this.linkLeft} name={this.left} />
            <ks-button rounded link={this.linkRight} name={this.right} />
        </Host>
    }
}